import { ElMessage } from 'element-plus';
import { toLine } from '@/utils/index';

export const useCopy = (text: string) => {
  navigator.clipboard
    .writeText(`el-icon${toLine(text)}`)
    .then(() => {
      ElMessage({
        message: '已复制到剪贴板!',
        type: 'success',
      });
    })
    .catch((error) => {
      ElMessage({
        message: '复制失败>_<!',
        type: 'error',
      });
    });
};
